<template>
  <div>
    <v-app-bar dense style="z-index:3">
      <v-btn text @click="filterSidebar()">
        <v-icon left>mdi-{{ filterIcon }}</v-icon >
        <template v-if="!mobile">{{ filterTitle }}</template>
      </v-btn>
      <v-spacer />
      <v-btn text  to="/">
        <v-icon left>mdi-cube</v-icon >
        <template v-if="!mobile">Decks</template>
      </v-btn>
      <v-btn text to="/cards/">
        <v-icon left>mdi-cards</v-icon>
        <template v-if="!mobile">Cards</template>
      </v-btn>
      <v-btn text to="/replays/">
        <v-icon left>mdi-play-circle</v-icon>
        <template v-if="!mobile">Replays</template>
      </v-btn>
      <v-spacer />
      <v-btn text @click="statsSidebar()">
        <v-icon left>mdi-chart-areaspline</v-icon>
        <template v-if="!mobile">Stats</template>
      </v-btn>
    </v-app-bar>
  </div>
</template>
<style>
</style>
<script>
export default {
  props: {
    filterSidebar: { type: Function },
    statsSidebar: { type: Function },
    filterIcon: { type: String },
    filterTitle: { type: String },
  },
  data () {
    return {
      mobile: false
    };
  },
  mounted () {
    if(this.$vuetify.breakpoint.name === "xs"){
      this.mobile = true;
    }
  }
}
</script>